import React, { useState, useEffect, useRef } from "react";

const Todos = () => {
  const [items, setItems] = useState([]);
  const [edit, setEdit] = useState(false);
  const [old, setOld] = useState("");
  const [msg, setMsg] = useState("");
  const [cancel, setCancel] = useState(false);

  const inputRef = useRef(null);

  const handleChange = e => {
    setMsg(e.target.value);
  };

  const handleEdit = item => {
    setEdit(true);
    setOld(item);
  };

  const handleUpdate = e => {
    e.preventDefault();
    const name = old;
    const newArr = [...JSON.parse(localStorage.getItem("items"))];
    const index = newArr.indexOf(name);
    if (cancel) {
      newArr[index] = name;
    } else {
      newArr[index] = msg;
    }
    localStorage.setItem("items", JSON.stringify(newArr));
    setOld("");
    setEdit(false);
    setMsg("");
    setCancel(false);
    setItems(newArr);
  };

  const handleDelete = item => {
    const newArr = [...JSON.parse(localStorage.getItem("items"))];
    const index = newArr.indexOf(item);
    newArr.splice(index, 1);
    localStorage.setItem("items", JSON.stringify(newArr));
    setEdit(false);
    setItems(newArr);
  };

  const handleSubmit = e => {
    e.preventDefault();
    const newArr = [...items, msg];
    localStorage.setItem("items", JSON.stringify(newArr));
    setMsg("");
    setItems(newArr);
  };

  useEffect(() => {
    const newArr = JSON.parse(localStorage.getItem("items"));
    if (newArr) {
      setItems(newArr);
    } else {
      setItems([]);
    }

    inputRef.current.focus();
  }, []);

  return (
    <div className="app__content">
      <h1>Imon's Todo--List</h1>

      <form onSubmit={edit ? handleUpdate : handleSubmit}>
        {edit ? (
          <div>
            <input
              type="text"
              placeholder="update..."
              className="input"
              onChange={handleChange}
              ref={inputRef}
            />
            <button
              type="submit"
              className="btn up__btn"
              onClick={() => setCancel(true)}>
              x
            </button>
            <button
              type="submit"
              disabled={msg.trim("").length <= 0}
              className="btn">
              update
            </button>
          </div>
        ) : (
          <div>
            <input
              type="text"
              placeholder="Add a Items"
              value={msg}
              className="input"
              onChange={handleChange}
              ref={inputRef}
            />
            <button
              type="submit"
              disabled={msg.trim("").length <= 0}
              className="btn">
              +
            </button>{" "}
          </div>
        )}
      </form>

      <div className="second__div">
        {items.map((item, index) => (
          <div className="dis__msg div" key={index}>
            <span className="click" onClick={() => handleEdit(item)}>
              {item}
            </span>
            <button onClick={() => handleDelete(item)} className="del">
              x
            </button>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Todos;
